import React from 'react'
import s from './Botox.module.css'
import ContentHeader from '../../../ContentHeader/ContentHeader'
import sign from './images/.png'
import question from './images/.png'
import syringe from './images/.png'

const Botox = () => {
	return (
		<div className={s.wrapper}>
			<div className='container'>
				<ContentHeader
					title={' /...'}
					link={'articlePermanent'}
					subtitle={' / '}
				/>
				<h1 className={s.title}>
					,    :   
					<img
						src={sign}
						width={35}
						height={35}
						alt=''
						className={s.iconTitle}
					/>
					<img
						src={question}
						width={35}
						height={35}
						alt=''
						className={s.iconQuestion}
					/>
				</h1>

				<p className={s.text}>
					        , 
					  -    .   
					   ,   .
				</p>
				<h3 className={s.subtitle}>
					<img src={syringe} alt='' height={30} width={30} className={s.icon} />
					    ?
				</h3>
				<p className={s.text}>  2 !</p>

				<p className={s.text}>
					        
					,      .   
					  ,     
					    .  ,   
					       ,
					         . 
					      ,  
					,     ,    
					 .    
					      ,    
					     .
				</p>
				<h3 className={s.subtitle}>
					<img src={sign} alt='' height={30} width={30} className={s.icon} />
					:    ,  .   
					,      ,   2 .
				</h3>

				{/* <div className={s.photo}>
				<img src={photo} className={s.photoImg} alt='' />
				</div> */}
			</div>
		</div>
	)
}

export default Botox
