import React from 'react'
import s from './Refresh.module.css'
import ContentHeader from '../../../ContentHeader/ContentHeader'
import circle from './images/.png'
import question from './images/.png'
import brows from './images/.jpeg'

const Refresh = () => {
	return (
		<div className={s.wrapper}>
			<div className='container'>
				<ContentHeader
					title={' /...'}
					link={'articlePermanent'}
					subtitle={' / '}
				/>
				<h1 className={s.title}>   </h1>
				<h3 className={s.subtitle}>
					  
					<img
						src={question}
						width={30}
						height={30}
						alt=''
						className={s.icon}
					/>
				</h3>
				<p className={s.text}>
					       . 
					,  /      .
					   ,    
					,       .
				</p>
				<p className={s.text}>
					     ,     . 
					   ,  
					:     .
				</p>
				<p className={s.text}>     :</p>
				<ul className={s.list}>
					<li>
						<img
							src={circle}
							alt=''
							height={20}
							width={20}
							className={s.icon}
						/>
						    ;
					</li>
					<li>
						<img
							src={circle}
							alt=''
							height={20}
							width={20}
							className={s.icon}
						/>
						      ;
					</li>
					<li>
						<img
							src={circle}
							alt=''
							height={20}
							width={20}
							className={s.icon}
						/>
						     ;
					</li>
					<li>
						<img
							src={circle}
							alt=''
							height={20}
							width={20}
							className={s.icon}
						/>
						  ;
					</li>
					<li>
						<img
							src={circle}
							alt=''
							height={20}
							width={20}
							className={s.icon}
						/>
						    .
					</li>
				</ul>
				<div className={s.photo}>
					<img src={brows} className={s.photoImg} alt='' />
				</div>
			</div>
		</div>
	)
}

export default Refresh
