import React from 'react'
import s from './Fixation.module.css'
import ContentHeader from '../../../ContentHeader/ContentHeader'
import question from './images/.png'
import face from './images/face.png'
import icon from './images/-.png'
import sign from './images/.png'

const Fixation = () => {
	return (
		<div className={s.wrapper}>
			<div className='container'>
				<ContentHeader
					title={' /...'}
					link={'articlePermanent'}
					subtitle={' / '}
				/>
				<h1 className={s.title}>
					    
					<img
						src={face}
						width={30}
						height={30}
						alt=''
						className={s.iconTitle}
					/>
					<img
						src={question}
						width={30}
						height={30}
						alt=''
						className={s.iconTitle}
					/>
				</h1>
				<p className={s.text}>  ,    :</p>
				<ul className={s.list}>
					<li>
						<img src={icon} alt='' height={30} width={30} className={s.icon} />
						  .   
						  .    ,   
						  -    .
					</li>
					<li>
						<img src={icon} alt='' height={30} width={30} className={s.icon} />
						     
						 .    ,    
						    .     
						 ,       .
					</li>
					<li>
						<img src={icon} alt='' height={30} width={30} className={s.icon} />
						       
						. ,      
						 -    -   .
					</li>
				</ul>
				<p className={s.text}>
					<img
						src={sign}
						alt=''
						height={30}
						width={30}
						className={s.iconSign}
					/>
					     
					<img
						src={sign}
						alt=''
						height={30}
						width={30}
						className={s.iconSign}
					/>
				</p>
			</div>
		</div>
	)
}

export default Fixation
