import React from 'react'
import s from './PeriodPage.module.css'
import ContentHeader from '../../../ContentHeader/ContentHeader'
import done from './images/icons8--94.png'
import thinking from './images/thinking-face.png'
import up from './images/icons8--100.png'
import question from './images/.png'

const PeriodPage = () => {
	return (
		<div className={s.wrapper}>
			<div className='container'>
				<ContentHeader
					title={' /...'}
					link={'articlePermanent'}
					subtitle={' / '}
				/>
				<h1 className={s.title}>
					   {' '}
					<img
						src={question}
						alt=''
						height={30}
						width={30}
						className={s.icon}
					/>
				</h1>
				<h3 className={s.subtitle}>
					  .  
					<img
						src={thinking}
						width={35}
						height={35}
						alt=''
						className={s.iconTitle}
					/>
				</h3>
				<p className={s.text}>
					      1  3 .  
					     :
				</p>
				<ul className={s.list}>
					<li>
						<img src={done} alt='' height={25} width={25} className={s.icon} />
						   .      
						 . ,   ,  
						   -  .   
						     .
					</li>
					<li>
						<img src={done} alt='' height={25} width={25} className={s.icon} />
						 .      
						 .     , 
						 ,   .
					</li>
					<li>
						<img src={done} alt='' height={25} width={25} className={s.icon} />
						  .    , 
						   .    , 
						       .    
						.
					</li>
					<li>
						<img src={done} alt='' height={25} width={25} className={s.icon} />
						  .      
						,    ,    
						.       
						  .
					</li>
					<li>
						<img src={up} alt='' height={30} width={30} className={s.icon} />
						           
						!
					</li>
				</ul>
			</div>
		</div>
	)
}

export default PeriodPage
