import React from 'react'
import s from './Correction.module.css'
import ContentHeader from '../../../ContentHeader/ContentHeader'
import sign from './images/.png'
import cycle from './images/--.png'
import woman from './images/.png'
import hand from './images/--.png'
import question from './images/.png'

const Correction = () => {
	return (
		<div className={s.wrapper}>
			<div className='container'>
				<ContentHeader
					title={' /...'}
					link={'articlePermanent'}
					subtitle={' / '}
				/>
				<h1 className={s.title}>
					   
					<img
						src={question}
						width={30}
						height={30}
						alt=''
						className={s.icon}
					/>
				</h1>
				<p className={s.text}>
					   ,     
					!!        
					 ,   .
				</p>

				<p className={s.text}>
					         
					.    ,   , 
					         
					          
					.
				</p>
				<p className={s.text}>
					<img src={cycle} alt='' height={35} width={35} className={s.icon} />
					   ,    !
				</p>
				<p className={s.text}>
					,   ,    - ,  ,
					,      ,  
					 !!!
				</p>
				<p className={s.text}>
					<img src={sign} alt='' height={25} width={25} className={s.icon} />
					  , , ,     
					    .
				</p>
				<p className={s.text}>
					 !
					<img src={woman} alt='' height={35} width={35} className={s.icon} />
					  !     
					<img
						src={hand}
						alt=''
						height={25}
						width={25}
						className={s.iconHand}
					/>
				</p>
			</div>
		</div>
	)
}

export default Correction
