import React from 'react'
import s from './ReactionDepilation.module.css'
import ContentHeader from '../../../ContentHeader/ContentHeader'
import honey from './images/.png'
import sign from './images/.png'
import circle from './images/.png'
import question from './images/.png'

const ReactionDepilation = () => {
	return (
		<div className={s.wrapper}>
			<div className='container'>
				<ContentHeader
					title={' /...'}
					link={'articleDepilation'}
					subtitle={' / '}
				/>
				<h1 className={s.title}>
					   
					<img
						src={honey}
						width={40}
						height={40}
						alt=''
						className={s.iconTitle}
					/>
				</h1>
				<p className={s.text}>
					      ,  
					  
					<img
						src={sign}
						width={25}
						height={25}
						alt=''
						className={s.iconSign}
					/>
				</p>
				<p className={s.text}>
					 ,      , 
					       ,  
					.
				</p>

				<h3 className={s.subtitle}>   :</h3>
				<h3 className={s.subtitle}>
					<img src={circle} width={20} height={20} alt='' className={s.icon} />
					 
				</h3>
				<p className={s.text}>
					        
					 ,        ,
					      .
				</p>
				<p className={s.textSubtitle}>
					 
					<img
						src={question}
						width={25}
						height={25}
						alt=''
						className={s.iconSign}
					/>
				</p>
				<p className={s.text}>
					     (/) 
					 .
				</p>
				<h3 className={s.subtitle}>
					<img src={circle} width={20} height={20} alt='' className={s.icon} />
					
				</h3>
				<p className={s.text}>
					      ,    20
					 - 2 .
				</p>
				<p className={s.textSubtitle}>
					 
					<img
						src={question}
						width={25}
						height={25}
						alt=''
						className={s.iconSign}
					/>
				</p>
				<p className={s.text}>
					  ,   ,    
					  .
				</p>
				<h3 className={s.subtitle}>
					<img src={circle} width={20} height={20} alt='' className={s.icon} />
					
				</h3>
				<p className={s.text}>
					    ,   .
				</p>
				<p className={s.textSubtitle}>
					 
					<img
						src={question}
						width={25}
						height={25}
						alt=''
						className={s.iconSign}
					/>
				</p>
				<p className={s.text}>
					  ,       
					,   .
				</p>
			</div>
		</div>
	)
}

export default ReactionDepilation
