import React from 'react'
import s from './BlueEyebrows.module.css'
import ContentHeader from '../../../ContentHeader/ContentHeader'
import question from './images/.png'
import smiley from './images/face.png'
import important from './images/.png'
import photo from './images/.jpeg'

const BlueEyebrows = () => {
	return (
		<div className={s.wrapper}>
			<div className='container'>
				<ContentHeader
					title={' /...'}
					link={'articlePermanent'}
					subtitle={' / '}
				/>
				<h1 className={s.title}>
					 {' '}
					<img
						src={smiley}
						width={35}
						height={35}
						alt=''
						className={s.iconSmiley}
					/>
					   
					<img
						src={question}
						width={30}
						height={30}
						alt=''
						className={s.icon}
					/>
				</h1>
				<p className={s.text}>
					    ,    
					 .      .
				</p>

				<p className={s.text}>
					    ?  ,  ,   
					        .
				</p>
				<p className={s.text}>
					    .    
					 ,     (  )   
					   .
				</p>
				<p className={s.text}>
					,     ,    : 
					   ?      . 
					        ( 
					).       .
				</p>
				<p className={s.text}>
					<img
						src={important}
						alt=''
						height={30}
						width={30}
						className={s.icon}
					/>
					.         , 
					    .    
					     ,    
					-.        .
				</p>

				<div className={s.photo}>
					<img src={photo} className={s.photoImg} alt='' />
				</div>
			</div>
		</div>
	)
}

export default BlueEyebrows
